<template lang="html">
  <panel :class="$style.panel">
      <div :class="$style.left"></div>
      <div :class="$style.center"> !</div>
      <div :class="$style.right"></div>
  </panel>
</template>

<script type="text/ecmascript-6">
import panel from "../core/panel.vue";
export default {
  components:{
    panel,
  }
};
</script>

<style lang="scss" module>
@import "../../css/element.scss";
.panel {
  @include panel;
  border-radius: 5px;
  >h4{
    display: none;
  }
  z-index: 2;
  position: relative;
  top: -60px;
  width: 92%;
  background-color: white;
  margin: 0 auto;
  padding: 32px 0;
  display: flex;
  justify-content: center;
  .left {
    min-width: 2.5rem;
    height: 1.8rem;
    line-height:1.8rem;
    color: #fff;
    font-size: 0.55rem;
    margin-left: 20px;
    text-align: center;
    background: url(data:image/png;base64,)
      50% no-repeat;
    background-size: cover;
    box-shadow: 0 0.1875rem 0.25rem 0 hsla(41, 44%, 63%, 0.51);
  }
  .center {
    text-align: center;
    font-family: PingFangSC-Semibold;
    font-size: 0.7rem;
    color: #c6a76f;
    letter-spacing: 0;
    line-height: 1.8rem;
    padding: 0 0.5rem;
    box-sizing: border-box;
  }
  .right {
    margin-right: 20px;
    text-align: center;
    width: 3.7rem;
    height: 1.8rem;
    line-height: 1.8rem;
    color: #fff;
    font-size: 0.7rem;
    background: orange;
    border-radius: 0.9375rem;
    background: url(data:image/png;base64,)
      50% no-repeat;
    background-size: cover;
    box-shadow: 0 0.25rem 0.375rem 0 hsla(40, 41%, 61%, 0.41);
  }
}
</style>



